'use strict';
/**
 * @api JUMP_OUT(url) JUMP_OUT
 * @apiGroup APP
 * @apiVersion 1.0.0
 * @apiParam {string} url 目标URL地址
 * @apiDescription 用外部浏览器打开 url
 * @apiUse APPNpm
 * @apiUse APPJs
 * @apiParamExample {string} 例子
 * JUMP_OUT('https://www.baidu.com')  外部地址
 * JUMP_OUT('https://gift.yingyinglicai.com/activity/invite/???')  活动地址
 * @apiParamExample {string} 返回值
 * 无
 **/

/**
 * todo JUMP
 */
import {WinJSDec, strError} from './APP/config';
import BROWSER from './BROWSER';

var name = 'JUMP_OUT';
var JUMP_OUT = url => {
  if (typeof url !== 'string') {
    throw new TypeError(`[${name}] ${strError}`);
  }
  if (BROWSER.isClient) {
    WinJSDec(_ => {
      window.WinJSBridge.call('navigation', 'openURLOutside', {url});
    });
  } else {
    window.location.href = url;
  }
};
export default JUMP_OUT;
